import React, { Component } from 'react';
import { Row, Col, Form, Checkbox, Input, message, Menu, Icon, Radio } from 'antd';
import { DatePickerString, SelectAjax } from '../../../components/common/index';
import { Btn, BtnClear } from '../../../components/common/button';
import { getTrainTypes } from '../../../services/trainType';
import { getStudyFields } from '../../../services/StudyField';
import { udpateQueryParams, getQueryParams } from '../../../utils/urlUtil';

@Form.create({ name: 'SearchBarStudent' })
class SearchBarStudent extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  getSemester = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Học kỳ 1',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLop = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Lớp A',
        },
        {
          id: '2',
          label: 'Lớp B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLanthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLoaithu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Thu SV nhập học',
        },
        {
          id: '2',
          label: 'Thu khác',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getHinhthucthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Hình thức A',
        },
        {
          id: '2',
          label: 'Hình thức B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getNamhoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '2018-2019',
        },
        {
          id: '2',
          label: '2019-2020',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDot = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDoituonghocphi = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Ngoài ngân sách',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getKhoahoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Khóa 2018',
        },
        {
          id: '2',
          label: 'Khóa 2019',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  exportExcel = () => {
    message.success('Demo disable when table data is empty');
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  menu = () => (
    <Menu onClick={this.handleMenuClick}>
      <Menu.Item key="Danh sách chi tiết biên lai">
        <Icon type="printer" />
        Danh sách chi tiết biên lai
      </Menu.Item>
      <Menu.Item key="Danh sách chi tiết biên lai phải thu">
        <Icon type="printer" />
        Danh sách chi tiết biên lai phải thu
      </Menu.Item>
      <Menu.Item key="Tổng hợp theo ngày và người thu">
        <Icon type="printer" />
        Tổng hợp theo ngày và người thu
      </Menu.Item>
    </Menu>
  );

  handleMenuClick = e => {
    message.info('Click on '.concat(e.key));
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const danophoso = getFieldValue('danophoso');
    const trainTypeId = getFieldValue('trainTypeId');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Hệ">
              {getFieldDecorator('trainTypeId', {})(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="CN">
              {getFieldDecorator('studyFieldId', {})(
                <SelectAjax
                  service={getStudyFields}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId }}
                  allowGetData
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Số BD">
              {getFieldDecorator('sobaodanh', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Họ và tên">
              {getFieldDecorator('hovaten', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Mã SV">
              {getFieldDecorator('masinhvien', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Ngày sinh">
              {getFieldDecorator('ngaysinh', {})(<DatePickerString placeholder="Ngày sinh" />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('danophoso', {
                valuePropName: 'checked',
                initialValue: danophoso,
              })(<Checkbox>Chỉ tìm những sv đã nộp hồ sơ, giấy tờ bên QLSV</Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('loaiphieu', {})(
                <Radio.Group setFieldsValue={setFieldsValue}>
                  <Radio value="daphanlop">Đã phân lớp</Radio>
                  <Radio value="chuaphanlop">Chưa phân lớp</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
          <Col span={6} style={{ float: 'right' }}>
            <div className="btn-group">
              <Btn htmlType="submit" title="Tìm kiếm SV" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBarStudent;



