import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Modal, Checkbox, Icon } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import SearchBarStudent from './SearchBarStudent';
import SearchBar from './SearchBar';
import { TableData } from '../../../components/common';
import { Btn } from '../../../components/common/button/index';
import { moneyToString } from '../../../utils/formatUtil';
import { getCurrentTuitionReceipts, getStudents } from '../../../services/currentTuitionReceipts';

class CurrentTuitionReceipts extends Component {
  state = {
    selectedStudent: {},
    visibleModal: false,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  setVisibleModal = visible => {
    this.setState({
      visibleModal: visible,
    });
  }

  columns = [
    {
      title: 'STT',
      dataIndex: 'index',
      key: 'index',
    },
    {
      title: 'Khoản nộp',
      dataIndex: 'khoannop',
      key: 'khoannop',
    },
    {
      title: 'Dư đầu kỳ',
      dataIndex: 'dudauky',
      render: text => moneyToString(text),
      key: 'dudauky',
    },
    {
      title: 'Phải nộp kỳ',
      dataIndex: 'phainopky',
      render: text => moneyToString(text),
      key: 'phainopky',
    },
    {
      title: 'Đã nộp kỳ',
      dataIndex: 'danopky',
      render: text => moneyToString(text),
      key: 'danopky',
    },
    {
      title: 'Số tiền',
      dataIndex: 'sotien',
      render: text => moneyToString(text),
      key: 'sotien',
    },
  ];

  columnsStudentSearch = [
    {
      title: 'Số báo danh',
      dataIndex: 'sobaodanh',
      key: 'sobaodanh',
    },
    {
      title: 'Mã sinh viên',
      dataIndex: 'masinhvien',
      key: 'masinhvien',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'hovaten',
      key: 'hovaten',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'ngaysinh',
      key: 'ngaysinh',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <Btn onClick={(e) => this.luachonsv(record, e)}>Lựa chọn SV</Btn>
        </Fragment>
      ),
    },
  ];

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleOk = () => {
    this.setState({
      visibleModal: false,
    });
  };

  handleCancel = () => {
    this.setState({
      visibleModal: false,
    });
  };

  luachonsv = value => {
    this.setState({
      visibleModal: false,
      selectedStudent: value,
    });
  }

  render() {
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User',
        name: record.name,
      }),
    };

    return (
      <Card>
        <Row>
          <Col span={24}>
            <SearchBar
              setVisibleModal={this.setVisibleModal}
              selectedStudent={this.state.selectedStudent}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              rowSelection={rowSelection}
              columns={this.columns}
              paramSearch={this.state.paramSearch}
              service={getCurrentTuitionReceipts}
              rowKey="index"
            />
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'center' }}>
            <Checkbox>In phiếu khi lưu</Checkbox>
            <Btn><Icon type="save" />Lưu phiếu</Btn>
          </Col>
        </Row>
        <Modal
          visible={this.state.visibleModal}
          width='80%'
          style={{ minWidth: '1200px' }}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          footer={[]}
        >
          <Row>
            <Col span={24}>
              <SearchBarStudent />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableData
                columns={this.columnsStudentSearch}
                paramSearch={this.state.paramSearch}
                service={getStudents}
                rowKey="index"
              />
            </Col>
          </Row>
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <CurrentTuitionReceipts />
  </Page>
);



