import React, { Component } from 'react';
import { Row, Col, Form, Checkbox, Input, message, Icon } from 'antd';

import moment from 'moment';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax, DatePickerString } from '../../../components/common/index';
import { Btn } from '../../../components/common/button';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;

    form.setFieldsValue(getQueryParams());
  };

  getSemester = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Học kỳ 1',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLop = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Lớp A',
        },
        {
          id: '2',
          label: 'Lớp B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLanthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDotthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLoaithu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Thu SV nhập học',
        },
        {
          id: '2',
          label: 'Thu khác',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getHinhthucthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Hình thức A',
        },
        {
          id: '2',
          label: 'Hình thức B',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getNamhoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '2018-2019',
        },
        {
          id: '2',
          label: '2019-2020',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDot = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getDoituonghocphi = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Ngoài ngân sách',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getLoaitien = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'VNĐ',
        },
        {
          id: '2',
          label: 'USD',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getKhoahoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Khóa 2018',
        },
        {
          id: '2',
          label: 'Khóa 2019',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  getThukhac = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Thu khác 1',
        },
        {
          id: '2',
          label: 'Thu khác 2',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  };

  setVisibleModal = () => {
    const { setVisibleModal } = this.props;
    setVisibleModal(true);
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  handleMenuClick = e => {
    message.info('Click on '.concat(e.key));
  };

  exportExcel = () => {
    message.success('Demo disable when table data is empty');
  };

  render() {
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const { selectedStudent } = this.props;

    const newDate = new Date();
    const date = newDate.getDate();
    const month = newDate.getMonth() + 1;
    const year = newDate.getFullYear();
    const separator = '/';
    const currentDate = `${date}${separator}${
      month < 10 ? `0${month}` : `${month}`
    }${separator}${year}`;

    const noptatca = getFieldValue('noptatca');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Năm học">
              {getFieldDecorator('namhoc', {})(
                <SelectAjax service={this.getNamhoc} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Học kì">
              {getFieldDecorator('hocki', {})(
                <SelectAjax service={this.getSemester} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Hình thức thu">
              {getFieldDecorator('hinhthucthu', {})(
                <SelectAjax service={this.getHinhthucthu} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={3}>
            <Form.Item {...formItemLayout} label="Lần thu">
              {getFieldDecorator('khoahoc', {})(
                <SelectAjax service={this.getLanthu} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={3}>
            <Form.Item {...formItemLayout} label="Đợt thu">
              {getFieldDecorator('dotthu', {})(
                <SelectAjax service={this.getDotthu} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Số phiếu">
              {getFieldDecorator('sophieu', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={1} style={{ textAlign: 'center' }}>
            <b>YHD15</b>
          </Col>
          <Col span={5} />
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Loại tiền">
              {getFieldDecorator('loaitien', {})(
                <SelectAjax service={this.getLoaitien} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Ngày thu">
              {getFieldDecorator('ngaythu', {
                initialValue: moment(currentDate, 'DD-MM-YYYY'),
              })(<DatePickerString placeholder="Ngày thu" />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Mã SV / SBD">
              {getFieldDecorator('masinhvien', {
                initialValue: selectedStudent.masinhvien,
              })(<Input readOnly />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Btn onClick={this.setVisibleModal}>
              <Icon type="search" />
            </Btn>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Họ tên">
              {getFieldDecorator('hoten', {
                initialValue: selectedStudent.hovaten,
              })(<Input readOnly />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Lớp">
              {getFieldDecorator('lop', {
                initialValue: selectedStudent.lop,
              })(<Input readOnly />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Form.Item {...formItemLayout} label="Nội dung">
              {getFieldDecorator('noidung', {})(<Input.TextArea style={{ height: '75px' }} />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Thu khác">
              {getFieldDecorator('thukhac', {})(
                <SelectAjax service={this.getThukhac} setFieldsValue={setFieldsValue} />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Số tiền">
              {getFieldDecorator('sotien', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Btn style={{ marginLeft: '10px' }}>
              Thêm <Icon type="plus" />
            </Btn>
            <Btn style={{ marginLeft: '10px' }}>
              Xóa <Icon type="delete" />
            </Btn>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Người thu">
              {getFieldDecorator('nguoithu', {
                initialValue: 'Phòng đào tạo',
              })(<Input readOnly />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <b style={{ marginRight: '20px' }}>Tổng số tiền:</b>
            <b style={{ color: 'red', fontSize: '15px' }}>3.670.000</b>
          </Col>
          <Col span={12}>
            <span style={{ color: 'red', fontSize: '15px' }}>
              (Ba triệu sáu trăm bảy mươi ngàn đồng)
            </span>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('noptatca', {
                valuePropName: 'checked',
                initialValue: noptatca,
              })(<Checkbox>Nộp tất cả</Checkbox>)}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



